import React, { Component } from 'react'

export default class DuaWrapper extends Component {
    state = {
        duas: [
            {
                id:undefined,
                title: 'Nermin',
                body: undefined,
                category: undefined,
                source: undefined,
                chain: undefined
            }
        ]
    }

    onHandleChange(e) {
        const value = e.target.value;
        const {duas} = this.state;
        const results = duas.filter(dua => {
            const regex = new RegExp(this.value, 'gi')
            return dua.name.match(regex) || dua.body.match(regex)
        })
        console.log(results);
    }

    render() {
        const duas = this.state.duas;
        return (
            <div>
                <FormComponent change={this.onHandleChange} />
            </div>
        )
    }
}

const FormComponent = (props) => {
    return (
        <form >
            <input type="text" name="title" onChange={props.change}/>
        </form>
    );
};





